<template>
	<div>
		<vue-headful title="Packages – FESTIVAL Pack'd" />
		<div class="container">
			<div class="row">
				<div class="col-md-3">
					<Nav />
				</div>
				<div class="col-md-9">
					<div class="dashhead mb-3">
						<div class="dashhead-titles">
							<h6 class="dashhead-subtitle">Packages</h6>
							<h2 class="dashhead-title">All Packages</h2>
						</div>
					</div>
					
					<div class="row mb-3">
						<div class="col-6 text-left">
							<button class="btn btn-secondary" @click="this.reloadTableData" :disabled="isDoingWork">
								<span class="icon icon-cw" />
								Refresh
							</button>
						</div>
						<div class="col-6 text-right">
							<router-link tag="button" class="btn btn-primary" to="/packages/new" :disabled="!packageData">
								<span class="icon icon-new-message" />
								New Package
							</router-link>
						</div>
					</div>
					
					<div class="row" v-if="isDoingWork">
						<div class="col col-12 mt-5 text-center">
							<div class="loading-indicator" />
						</div>
						<div class="col col-12 text-center">
							<div class="loading-indicator-label">Loading</div>
						</div>
					</div>
					
					<div v-if="!isDoingWork && packageData">
						<div class="table-responsive">
							<table class="tablesort" data-sort="table" data-sortlist="[[4,1]]">
								<thead>
									<tr>
										<th class="header">Name</th>
										<th class="header sorter-false d-none d-md-table-cell">Version</th>
										<th class="header sorter-false d-none d-md-table-cell">Downloads</th>
										<th class="header sorter-false">Visible</th>
										<th class="header sorter-mmddyy d-none d-md-table-cell">Updated</th>
										<th class="sorter-false text-right">Actions</th>
									</tr>
								</thead>
								<tbody v-if="packageData.length">
									<router-link tag="tr" :to="`/packages/${packageObj.identifier}`" :key="packageObj.id" v-for="packageObj in packageData">
										<td>
											<p class="mb-0">{{ packageObj.name }}</p>
											<p class="mb-0">
												<small>{{ packageObj.identifier }}</small>
											</p>
											<p class="mb-0 d-md-none">
												<small>{{ packageObj.latestVersion.version }}</small>
											</p>
											<p class="mb-0 d-md-none">
												<small>{{ packageObj.downloadCount | number }} Download{{ packageObj.downloadCount != 1 ? 's' : '' }}</small>
											</p>
										</td>
										<td class="d-none d-md-table-cell">{{ packageObj.latestVersion.version }}</td>
										<td class="d-none d-md-table-cell">{{ packageObj.downloadCount | number }}</td>
										<td>
											<SwitchCheckbox v-model="packageObj.visible" />
										</td>
										<td class="d-none d-md-table-cell">{{ packageObj.updatedAt | date }}</td>
										<td class="text-right">
											<button class="btn btn-outline-danger ml-2" @click.prevent="deletePackage(packageObj.id)">
												<span class="icon icon-trash" />
											</button>
										</td>
									</router-link>
								</tbody>
							</table>
						</div>
						<p v-if="!packageData.length || packageData.status == 404">There are no packages to display. <router-link to="/packages/new">Create a package</router-link></p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<style lang="less" scoped>
@media all and (max-width: 320px) {
	.table-responsive {
		td .btn:not(:last-child) {
			margin-bottom: .5rem;
		}
	}
}


.table-responsive {
	tbody tr {
		cursor: pointer;

		&:hover {
			color: #cfd2da;
			text-decoration: none;
			background-color: #434857;
		}
	}
	
	
	td small {
		color: #999;
	}
}
</style>

<script>
import { PackageAPI } from "@/scripts/ApiUtil"
import UserRole from "../../../server/UserRole"
import Nav from '@/components/Nav.vue'
import SwitchCheckbox from '@/components/SwitchCheckbox.vue'

export default {
	name: "packages",
	components: {
		Nav,
		SwitchCheckbox
	},
	data() {
		return {
			packageData: null,
			isDoingWork: false
		}
	},
	async created() {
		this.reloadTableData();
	},
	mounted() {
		window.scrollTo(0,0);
	},
	filters: {
		date(value) {
			return new Date(value).toLocaleDateString("en-US");
		},
		number(value) {
			return new Intl.NumberFormat("en-US").format(value);
		}
	},
	methods: {
		async reloadTableData() {
			this.isDoingWork = true;
			
			let _packageData = await PackageAPI.getPackages();
			if (_packageData.status == 200) {
				if ((this.$store.getters.userRole & UserRole.DEVELOPER) == UserRole.DEVELOPER && this.$store.getters.userRole < UserRole.MODERATOR) {
					this.packageData = _packageData.data.filter(_ => _.accountId == this.$store.getters.accountId).sort((a, b) => b.downloadCount > a.downloadCount);
				} else if (this.$store.getters.userRole >= UserRole.MODERATOR) {
					this.packageData = _packageData.data.sort((a, b) => b.downloadCount > a.downloadCount);
				}
				
				this.isDoingWork = false;
			} else {
				this.packageData = {
					status: _packageData.status
				}
				this.isDoingWork = false;
			}
			
			setTimeout(() => {
				$('[data-sort="table"]').tablesorter({
					//sortList: [[1, 1]]
				});
			}, 0);
		},
		async deletePackage(packageId) {
			const self = this;
			bootbox.confirm({
				title: "Delete Package?",
				message: "Are you sure you want to delete this package? This action cannot be undone.",
				buttons: {
					confirm: {
						label: 'Delete',
						className: 'btn-danger'
					},
					cancel: {
						label: 'Cancel',
						className: 'btn-secondary'
					}
				},
				callback: async (result) => {
					if (result) {
						let _packageDelete = await PackageAPI.deletePackage(packageId);
						if (_packageDelete.status != 200) return alert(`There was an error deleting this package.<br><br>${_packageDelete.data.message}`);
						
						self.reloadTableData();
					}
				}
			});
		}
	}
}
</script>